___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 768px) {
    /* Header Stacks */
    nav > ul {
        gap: 15px;
        width: 100%;
    }

    nav > ul > li {
        font-size: 1rem;
        padding: 8px;
    }

    /* Profile switches to vertical */
    #container-profile {
        flex-direction: column-reverse; /* Image on top, text below */
        text-align: center;
        padding: 30px 20px;
        height: 900px;
    }

    #text-section,
    #profile-section {
        width: 90%; /* Take up most of the screen width */
    }

    #text-section {
        align-items: center; /* Center text and button */
    }

    #profile-section > img {
        height: auto; /* keeps aspect ratio */
        display: block;
        border-radius: 50%;
    }

    /* Projects switch to single column */
    #projects {
        grid-template-columns: 1fr;
        padding-top: 100px;
    }

    #project-head {
        position: relative;
        padding: 20px;
        top: 0px;
    }

    .project-items {
        border-radius: 2px;
        box-shadow: 1px 2px var(--color-3);
        color: var(--color-black);
        background-color: var(--color-4);
        display: flex;
        flex-direction: column;
        gap: 30px;
        padding: 30px;
        width: 300px;
        height: auto;
    }

    .project-items > img {
        max-width: 100%;
    }

    .project-infos {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }

    .project-infos > h2 {
        font-size: 1rem;
    }

    .project-infos > p {
        font-size: 0.6rem;
    }

    #footer {
        flex-direction: column;
        height: auto;
        gap: 30px;
        padding: 40px;
    }
}
