const projects = document.getElementById("projects");

const assets = require.context("../assets/images", false, /\.(png|jpe?g|svg)$/);

const githubIconSource = assets("./github.svg");

const createProjectInfo = () => {
    let title = document.createElement("h2");
    let desc = document.createElement("p");

    return {
        title,
        desc,
    };
};

const createProject = (filename, name, description, link) => {
    let div = document.createElement("div");
    div.classList.add("project-items");

    let image = document.createElement("img");
    image.src = `${filename}`;
    image.alt = "filename";

    let divText = document.createElement("div");
    divText.classList.add("project-infos");

    let info = createProjectInfo(name, description);

    info.title.textContent = name;
    info.desc.textContent = description;
    let icon = createGithubIcon(githubIconSource);

    icon.addEventListener("click", () => {
        window.open(link, "_blank");
    });

    divText.append(info.title, info.desc, icon);

    div.append(image, divText);

    projects.appendChild(div);
};

const createGithubIcon = (src) => {
    let icon = document.createElement("img");
    icon.classList.add("github-icon");
    icon.src = src;

    return icon;
};

export { createProject };
