    --color-black: #000000;
    --color-2: #14213d;
    --color-3: #fca311;
    --color-4: #e5e5e5;
    --color-5: #ffffff;
}

@font-face {
    font-family: "Merriweather";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: "Roboto";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

@font-face {
    font-family: "RobotoV";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

@font-face {
    font-family: "MWBold";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

/* GENERAL STYLES*/
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Roboto";
    font-size: 18px;
}

html,
body {
    min-height: 100%;
}

/* MAIN STYLES */

/* HEADER */
header {
    background-color: var(--color-2);
    width: 100%;
    height: 20%;
    padding: 30px 35px;
    display: flex;
    justify-content: center;
    align-items: center;
}

nav > ul {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 40px;
    width: 700px;
}

nav > ul > li {
    color: var(--color-5);
    cursor: pointer;
    font-family: "Merriweather";
    font-size: 1.3rem;
    list-style-type: none;
    padding: 20px 10px;
    text-decoration: none;
}

nav > ul > li:hover {
    background-color: var(--color-3);
}

/* MAIN CONTENT */

main {
    background-color: var(--color-black);
    animation: fadeIn 1.5s ease forwards;
    width: 100%;
    height: auto;
}

#container-profile {
    background-color: var(--color-5);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;
    height: 600px;
    padding: 20px;
}

#text-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 20px;
    width: 40%;
}

#text-section > * {
    color: var(--color-black);
    font-size: 1.1rem;
}

#name-title {
    font-family: "Merriweather";
    font-size: 2.6rem;
}

/* Contact BUtton */
#contact {
    border: 2px solid var(--color-3);
    background-color: var(--color-3);
    margin-top: 10px;
    padding: 10px 25px;
    font-family:
        "MWBold",
        system-ui,
        -apple-system,
        BlinkMacSystemFont,
        "Segoe UI",
        Roboto,
        Oxygen,
        Ubuntu,
        Cantarell,
        "Open Sans",
        "Helvetica Neue",
        sans-serif;
    font-size: 1.5rem;
}

#contact:hover {
    background-color: var(--color-black);
    color: var(--color-5);
    cursor: pointer;

    /* transition effect */
    transition: all 0.5s ease-out;
}

#profile-section {
    width: 25%;
}

#profile-section > img {
    max-width: 100%;
    height: auto; /* keeps aspect ratio */
    display: block;
    border-radius: 50%;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

#projects {
    background-color: var(--color-2);
    color: var(--color-5);
    position: relative;
    padding: 60px 30px;
    padding-top: 150px;
    width: 100%;
    display: grid;
    justify-items: center;
    justify-content: center;
    align-items: center;
    align-content: center;

    grid-template-columns: 1fr 1fr;
    gap: 50px;
    align-items: start;
    grid-auto-rows: auto;
}

#project-head {
    position: absolute;
    font-family: "RobotoV", "Courier New", Courier, monospace;
    font-size: 2.4rem;
    font-weight: 800;
    top: 40px;
}

.project-items {
    border-radius: 20px;
    box-shadow: 5px 5px 2px var(--color-3);
    color: var(--color-black);
    background-color: var(--color-4);
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 30px;
    width: 700px;
    height: auto;
}

.project-items > img {
    max-width: 100%;
}

.project-infos {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.project-infos > h2 {
    font-size: 1.7rem;
}

.github-icon {
    margin-top: 50px;
    align-self: center;
    width: 30px;
    height: auto;
}

.github-icon:hover {
    cursor: pointer;
    transform: scale(1.2);
}

#footer {
    background-color: var(--color-5);
    color: var(--color-black);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 25px;
    width: 100%;
    height: 300px;
}

#footer > #image-links {
    justify-self: center;
}

#footer > #image-links > img {
    width: 30px;
    height: auto;
}
