___CSS_LOADER_EXPORT___.push([module.id, `/* TABLET STYLES */
@media screen and (min-width: 769px) and (max-width: 1024px) {
    /* 1. Header: Keep horizontal but tighter */
    nav > ul {
        width: 100%;
        gap: 20px; /* Smaller gap than desktop */
    }

    nav > ul > li {
        font-size: 1.1rem;
        padding: 10px 5px;
    }

    /* 2. Profile: Side-by-side but scaled */
    #container-profile {
        flex-direction: row; /* Keep them side-by-side */
        height: auto;
        padding: 60px 20px;
        gap: 30px;
    }

    #text-section {
        width: 50%; /* Give text more room than on desktop */
    }

    #profile-section {
        width: 35%; /* Make image slightly larger for tablet */
    }

    #name-title {
        font-size: 2.2rem; /* Shrink title slightly */
    }

    /* 3. Projects: 2 Columns */
    #projects {
        grid-template-columns: 1fr; /* Two equal columns */
        gap: 20px;
        padding: 80px 20px;
    }

    #project-head {
        position: relative;
        margin-bottom: 30px;
        top: 0px;
    }

    .project-items {
        width: 95%; /* Let them fill the grid cell */
        max-width: none; /* Remove desktop width restrictions */
    }
}
